* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Heebo", "Arial", sans-serif;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.body {
  margin: 0;
  padding: 0;
  background-color: var(--bg);
  color: var(--primary-text);
}
.body--scrolloff {
  overflow: hidden;
}

@font-face {
  src: url("../fonts/Heebo-Bold.woff2") format("woff2"), url("../fonts/Heebo-Bold.woff") format("woff");
  font-weight: 700;
  font-family: "Heebo";
  font-display: swap;
}
@font-face {
  src: url("../fonts/Heebo-SemiBold.woff2") format("woff2"), url("../fonts/Heebo-SemiBold.woff") format("woff");
  font-weight: 600;
  font-family: "Heebo";
  font-display: swap;
}
@font-face {
  src: url("../fonts/Heebo-Medium.woff2") format("woff2"), url("../fonts/Heebo-Medium.woff") format("woff");
  font-weight: 500;
  font-family: "Heebo";
  font-display: swap;
}
@font-face {
  src: url("../fonts/Heebo-Regular.woff2") format("woff2"), url("../fonts/Heebo-Regular.woff") format("woff");
  font-weight: 400;
  font-family: "Heebo";
  font-display: swap;
}
@font-face {
  src: url("../fonts/Heebo-Light.woff2") format("woff2"), url("../fonts/Heebo-Light.woff") format("woff");
  font-weight: 300;
  font-family: "Heebo";
  font-display: swap;
}
/* blocks */
.theme-switcher {
  width: 106px; /* padding 5px + (32px icon size * 3 icons) */
  position: relative;
  margin: 0;
  padding: 5px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  border: none;
}
.theme-switcher:hover {
  box-shadow: var(--inner-shadow);
}

/* Switcher Radio */
.theme-switcher__radio {
  cursor: pointer;
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
  width: 32px;
  height: 32px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px;
  transition: filter 0.1s ease-in;
}
.theme-switcher__radio:focus {
  outline: none;
}
.theme-switcher__radio--light {
  background-image: url("../images/icons/light.svg");
}
.theme-switcher__radio--auto {
  background-image: url("../images/icons/auto.svg");
}
.theme-switcher__radio--dark {
  background-image: url("../images/icons/dark.svg");
}

/* Switcher Status */
.theme-switcher__status {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  background-repeat: no-repeat;
  background-image: url("../images/icons/status.svg");
  background-size: 30px;
  background-position: center;
  transition: background-position 0.1s ease-in;
}

.theme-switcher__radio:focus-visible ~ .theme-switcher__status {
  box-shadow: 0 0 0 2px black;
}

.theme-switcher__radio--light:checked ~ .theme-switcher__status {
  background-position: left 6px center;
}

.theme-switcher__radio--auto:checked ~ .theme-switcher__status {
  background-position: center center;
}

.theme-switcher__radio--dark:checked ~ .theme-switcher__status {
  background-position: right 6px center;
}